nav {
    width: 80vw;

    display: flex;
    margin: auto;
    align-items: center;
    justify-content: center;
    padding-bottom: 8vh;
}

nav ul {
    margin: auto;
}
.menu-icon {
    display: inline-block;
    position: absolute;
}
.dropdown-content {
    display: none;
    z-index: 1;
    position: relative;
}

.menu-icon:hover .dropdown-content {
    display: block;
}

nav li {
    display: block;
    padding: 0.125em;
}
nav li a {
    font-family: Garamond, Arial, sans-serif;
    color: white;
    text-decoration: none;
    font-size: 1.5em;

    background-color: black;
}
nav li a.current {
    color: black;
    background-color: white;
}

nav li a:hover {
    color: black;
    background-color: white;
}
